.Box:hover{
    transform: scale(1.1);
    transition-duration: .5s;
    
}
.Box{
    border-radius:1rem;
    margin:auto
}
.social:hover{
    transform: scale(1.1);
    transition-duration: .5s;
    
}

.type{
    overflow: hidden;
    border-right:solid black;
    white-space: nowrap;
    width: 100;
    animation:typing 10s forwards;
}

@keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }